<template>
  <div class="curriculumRecovery">
    

    <div>
      <p style="display: inline-block"></p><span>20</span>
      <el-input v-model="input" size="small" placeholder="" style="width: 14%"></el-input>
      <el-input v-model="input" size="small" placeholder="" style="width: 14%"></el-input>
      <el-select v-model="value" size="small" placeholder="" style="width: 14%">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <div class="block" style="width: 30%;display: inline-block" >
       <!-- {{value6}}-->
        <el-date-picker
          style="width: 100%"
          size="small"
          v-model="value6"
          type="daterange"
          range-separator=""
          start-placeholder=""
          end-placeholder="">
        </el-date-picker>
      </div>
      <el-button type="primary" size="mini" style="float: right;margin-left: 1%"></el-button>
      <el-button type="primary" size="mini" style="float: right;margin-left: 1%"></el-button>
    </div>
    <div>
      <el-table
        ref="multipleTable"
        :data="tableData3"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="30">
        </el-table-column>

        <el-table-column
          prop="lessonName"
          label="Lession"
          min-width="30%">
        </el-table-column>
        <el-table-column
          prop="courseName"
          label="Course"
          min-width="40%">
        </el-table-column>
        <el-table-column
          prop="createUserName"
          label=""
          min-width="30%">
        </el-table-column>
        <el-table-column
          prop="materialType"
          label=""
          min-width="30%">
        </el-table-column>
        <el-table-column
          prop="fileSize"
          label="Size"
          min-width="30%">
        </el-table-column>
        <el-table-column
          prop="updateTime"
          label="Update"
          min-width="50%">
          <template slot-scope="scope">{{ scope.row.updateTime }}</template>
        </el-table-column>
        <el-table-column
          prop="downloadCount"
          label=""
          min-width="30%">
        </el-table-column>

        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--<div style="margin-top: 20px">
        <el-button @click="toggleSelection([tableData3[1], tableData3[2]])"></el-button>
        <el-button @click="toggleSelection()"></el-button>
      </div>-->
    </div>
    <div style="position: absolute;bottom: 8%;left: 44%">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="1000">
      </el-pagination>
    </div>

  </div>
</template>
<!--/teacher/pageList-->
<script>
  export default {
    data() {
      return {
        input:'',
        tableData3: [{
          lessonName: 'lession1',
          courseName:'',
          createUserName:'admin',
          fileSize:'100M',
          materialType:'',
          downloadCount:'88',
          updateTime:'2018-12-12 20:20PM',


        }, {
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-04',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-07',
          name: '',
          address: ' 1518 '
        }],
        multipleSelection: [],
        options: [{
          value: '1',
          label: ''
        },  {
          value: '2',
          label: ''
        }],
        value: '',
        value6: '',
      }
    },
    methods: {
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleEdit(index, row) {
        console.log(index, row);
      },
      handleDelete(index, row) {
        console.log(index, row);
      }
    }
  }
</script>

<style scoped="">
  .curriculumRecovery {
    margin: 2%;
    margin-top: 0px;
    padding-top: 2%;
  }
  .end-placeholder {
    margin-left: 6% !important;
  }
</style>





